<script setup lang="ts">
import {AuthService} from '@/services/auth.service';

const service = useService(AuthService);
const route = useRoute();
const params = new URL(route.fullPath, 'https://example.com/').searchParams;
const code = params.get('code');
const error = params.get('error');
const errorCode = params.get('error_code');
const errorDescription = params.get('error_description');
const data = {code, error, errorCode, errorDescription};

onMounted(() => service.verifyMagicLink(data));
</script>
<template>
  <div>Производится вход...</div>
</template>
